.backdrop{
    position: fixed;
    top:0;
    left:0;
    width: 100%;
    height: 100vh;
    z-index: 10;
    background: rgba(0,0,0,0.15);
  }
  
  .modal{
      
      border-radius: 10px;
      background-color: rgba(255, 255, 255, 0);
      position: fixed;
      z-index:10;
      width: 20rem;
      top: 50%;
      left: 40.6%;
      max-height: 20rem;
      overflow: hidden;
      margin-left: -200px; /* Negative half of width. */
  }
  
  .header{
      background: #4f0056;
      padding: 1rem;
  }
  
  .header h2{
      margin: 0;
      color: white;
  }
  
  .content{
      padding: 1rem;
  }
  
  .actions{
      padding: 1rem;
      display: flex;
      justify-content: flex-end;
  }
  
  @media (min-width:768px){
     .modal{
         left: calc(50%-20rem);
         width: 40rem;
     }
  }
  