#container-bg{
  background: #e4e5e6;
  padding: 30px 0px;
}

#wrapper-box{
  padding: 25px;
  margin: 15px;
  background-color:#fff ;
}

#title-heade{
  color: #000;
  font-weight: 700;
  margin-bottom: 15px;
}

.tb-space{
  padding: 20px 0px ;
}


.box {
  display: flex;
  flex-direction: 'row';
  flex-wrap: 'wrap';
  align-items: 'flex-start' ;
}

.one {
  flex: 1;
  display: flex;
  flex-direction: column;
  width: 50%;
}
.leftCheckBox {
  display: flex;
  flex-direction: row;
  flex: 0.7;
  height: 30px;
  
}

.rightSaveButton {
  flex: 0.3;
  height: 30px;
  margin-right: 2.3%;
  margin-top: 1rem;
  flex-direction: row-reverse;
  display: flex;
  align-items: flex-end;
  
}

.rightSearchButton {
  flex: 0.3;
  height: 30px;
  flex-direction: row-reverse;
  display: flex;
  align-items: flex-end;
}

.centerSearch{
  height: 30px;
}

.centerSearch p{
  margin-top: 5px;
}

.select{
 margin-right: 10%;
 height: 2rem;
 margin-top: 0.5rem;
}

.selectItem{
  margin-right: 1%;
  height: 2rem;
  margin-left: 0.5rem;
  width: 3rem;
 }

.itemsSelect{
  height: 2rem;
}

.itemsSelect p{
  margin-top: 5px;
  margin-left: 0px;
}

.marginTop{
  margin-top: 1rem;
  margin-left: 0rem;
}

.marginTopActive{
  margin-top: 0.8rem;
  margin-left: 0.2rem;
  font-size: 1rem;
}

