      padding: 8px 8px;
      background: green;
      width: 1px;
      height: 10px;
      border-radius: 100%;
      text-align: center;
      align-items: center;
  }

  .circle_red
  {
      border: 0px solid #a1a1a1;
      padding: 8px 8px;
      background: rgb(128, 0, 0);
      width: 1px;
      height: 10px;
      border-radius: 100%;
      text-align: center;
      align-items: center;
  }
  /* $bg-color: '#010101';
  $font-color: #F0F0F0;
  $pencil-color-hover: #77FF77; */
  .edit-pencil {
    position: relative;
    display: none;
    
    width: 25px;
    height: 36px;
    margin-top: -8px;
    padding: 0 7px;
    font-size: 0;
    vertical-align: middle;
    cursor: pointer;
  }
  .edit-pencil:before {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-2px, -3px) rotate(-45deg);
    width: 3px;
    height: 5px;
    background-color: #ff0000;
    box-shadow: 1px 0px 0px #ff0000, 2px 0px 0px #ff0000, 3px 0px 0px #ff0000, -1px 0px 0px #ff0000, -2px 0px 0px #ff0000, -3px 0px 0px #ff0000, -3.3px 0px 0px #ff0000, 7px 0px 0px #ff0000;
    transition: all 300ms;
    content: '';
  }
  .edit-pencil:after {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-9px, 3px) rotate(-45deg);
    font-size: 1px;
    border: solid 3em transparent;
    border-left-width: 0;
    border-right-width: 5em;
    border-right-color: #ff0000;
    transition: all 300ms;
    content: '';
  }
  .edit-pencil:hover:before {
    background-color: #ff0000;
    box-shadow: 1px 0px 0px #ff0000, 2px 0px 0px #ff0000, 3px 0px 0px #ff0000, -1px 0px 0px #ff0000, -2px 0px 0px #ff0000, -3px 0px 0px #ff0000, -3.3px 0px 0px #ff0000, 7px 0px 0px #ff0000;
  }
  .edit-pencil:hover:after {
    border-right-color: #ff0000;
  }

  .divIconRow{
    display: flex;
    padding-top: 10px;
    
  }

 

  .divIconRow:hover .edit-pencil{
    visibility: visible;
    display: inline-block;
  }
  .circle_green:hover .edit-pencil{
    visibility: visible;
    display: inline-block;
  }
  .deleteStyle{
    font-size: 20px;
    color: red;
    margin-top: -2px;
    display: none;
    cursor: pointer;
  }
  .divIconRow:hover .deleteStyle{
    visibility: visible;
    display: block;
  }

  .circle_green:hover .deleteStyle{
    visibility: visible;
    display: block;
  }

 
  

  
 

